// import 'bootstrap/dist/css/bootstrap.min.css';
// import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import React, {useState, useEffect} from 'react';
import axios from 'axios';
import Header from './Header';
// import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { Link } from 'react-router-dom';
function ActivationUser() {    
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const uid = searchParams.get('uid');
    const token = searchParams.get('token');
  
    useEffect(() => {
      if (uid && token) {
        //  POST-   Axios   
        //  uid  token   
        const requestData = { uid, token };
        const confirmationUrl = 'http://localhost:8000/auth/users/activation/';
        //  POST-   Axios
        axios.post(confirmationUrl, requestData)
        .then(response => {
            //    
            console.log(response.data);
            // ,    -     
        })
        .catch(error => {
            //  
            console.error('Error:', error);
        });
      }
    }, [uid, token]);
  
  return (
    <div className="">
        <Header/>
        <h5 className='h1'>
              
        </h5>
        <p className='text-primary text-left'> <Link to="/calculator">   </Link></p>
    </div>
  );
  }

export default ActivationUser;