
import {Modal, Form, Button } from 'react-bootstrap';
import axios from 'axios';
// import Modal from 'react-modal';
import { useState } from 'react';
import { useObserver } from 'mobx-react';
import AuthStore from './AuthStore';
import { useContext } from 'react';
import { observer } from 'mobx-react';
const ModalAuth = observer(({Store, showModal, handleModalClose, openRegClick, show, onClose,isOpen}) =>{
  // const authStore = useContext(AuthStore);
  console.log(showModal)
  // if (!showModal) {
    
  //   return null;
  // }
  const id='modalAuth'
  const handleClick =()=>{
    openRegClick();
    handleModalClose();
  }
  const handleLogin = (e) => {
    e.preventDefault();
    //     
    // ...
  };

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [checkLogin, setcheckLogin] = useState(true);

  const handleSubmit = (e) => {
    e.preventDefault();

    const data = {
      username: username,
      password: password,
    };
    console.log(data)
    const UserData = JSON.stringify(data);
    axios({
      method: 'post',
      url: 'http://127.0.0.1:8000/auth/token/login/',
      data: UserData,
      headers: {
        'Content-Type': 'application/json',
      },
    })
    
      .then(response => {
        //   
        setcheckLogin(true)
        console.log(' :', response.data);
        localStorage.setItem('authToken', response.data.auth_token);
        Store.login()
        handleModalClose();
      })
      .catch(error => {
        //  
        setcheckLogin(false)
        console.error(' :', error);
      });
  };

  
    
  
    
  return (
    
  //   <Modal
  //     isOpen={isOpen}
  //     onRequestClose={onClose}
  //     contentLabel=" "
  //   >
  //     <div className="modal" tabIndex="-1" id={id}>
  //     <div className="modal-dialog">
  //       <div className="modal-content">
  //         <div className="modal-header">
  //           <h5 className="modal-title">  </h5>
  //           <button type="button" className="btn-close" onClick={handleModalClose} aria-label="Close"></button>
  //         </div>
  //         <div className="modal-body">
  //           {/*     */}
  //         </div>
  //         <div className="modal-footer">
  //           <button type="button" className="btn btn-secondary" onClick={handleModalClose}></button>
  //           <button type="button" className="btn btn-primary"></button>
  //         </div>
  //       </div>
  //     </div>
  // </div>
  //   </Modal>


      
      <Modal show={showModal} onHide={handleModalClose}>
        <Modal.Header closeButton>
          <Modal.Title>  </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={handleSubmit}>
            <Form.Group controlId="formBasicUserName">
              <Form.Label></Form.Label>
              <Form.Control type="text" value={username} onChange={(e) => setUsername(e.target.value)} placeholder=' '/>
              <Form.Group controlId="formBasicPassword" style={{paddingBottom: '10px'}}>
                <Form.Label></Form.Label>
                <Form.Control type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
              </Form.Group>
              {/* <Form.Text className="text-muted">
                       - .
              </Form.Text> */}
            </Form.Group>
            {!checkLogin && (
              <Form.Group style={{alignItems: 'center', display: 'flex', justifyContent: 'center', color: 'red'}}>
                <p>   </p>
              </Form.Group>
            )}
            

            
            <Form.Group style={{alignItems: 'center', display: 'flex', justifyContent: 'center'}}>
                <Button variant="primary" type="submit" style={{alignItems: 'center', display: 'flex', justifyContent: 'center', width: '200px', padding:'5px'}} >
                    
                </Button>
            </Form.Group>
            <Form.Group style={{alignItems: 'center', display: 'flex', justifyContent: 'center',}}>
                <Form.Label>  ?</Form.Label>
                <Form.Label style={{color: '#0D6EFD'}}></Form.Label>
            </Form.Group>
            
          </Form>
        </Modal.Body>
      </Modal>
    
  );
})

export default ModalAuth;