// import 'bootstrap/dist/css/bootstrap.min.css';
// import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import React, {useState, useEffect} from 'react';
import axios from 'axios';
import Header from './Header';
import CountSum from './CountSum';
import AuthStore from './AuthStore';
axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
function Calculator( {Store} ) {
    const [Probeg, setProbeg] = useState(0);
    const [Rashod, setRashod] = useState(0);
    const [FuelPrice, setFuelPrice] = useState(0);
    const [year, setYear] = useState(0);
    const [Modification_id, setModification_id] = useState(0);
    const [brand, setBrand] = useState('');
    const [model, setModel] = useState('');
    const [BrandList, setBrandList] = useState([]);
    const [ModelList, setModelList] = useState([]);
    const [Summa, setSumma] = useState([]);
    const [ModificatioinList, setModificationList] = useState([]);
    const [RegionList, setRegionList] = useState([]);
    const [Region, setRegion] = useState([]);
    const [checkSum, setcheckSum] = useState(false);
    var storedToken = localStorage.getItem('authToken');
    useEffect(() => {
      handleMarkaGet();
      handleRegionGet();
      storedToken = localStorage.getItem('authToken');

      if (storedToken) {
        Store.login();
      }
    }, []);
    
    const handleProbegChange = (probeg) => {
      setProbeg(probeg);
    }
    const handleRashodChange = (rashod) => {
      setRashod(rashod);
    }
    const handleCostChange = (cost) => {
      setFuelPrice(cost);
    }
    const handleBrandChange = (selectedBrand) => {
      setBrand(selectedBrand);
    }
    const handleModelChange = (selectedModel) => {
      setModel(selectedModel);
    }
    const handleModificationIdChange = (id) => {
      setModification_id(id);
    }
    const handleRegionChange = (region) => {
      setRegion( region);
    }
    const handleCheckSumChange = () => {
      setcheckSum(true);
    }
    const handleSumView =() =>{
      return <CountSum/>
    }
    //   
    function handleMarkaGet (selectedBrand) {
      // const selectedBrand = event.target.value;
      // setSelectedBrand(selectedBrand);
      // const selectedBrand=e.target.value;
      setBrand(selectedBrand);
      console.log(brand)
      // axios
      //   // .get(`/api/models?brand=${selectedBrand}`)
      //   .get(`http://localhost:8000/brands`, {
      //     headers: {
      //       'Access-Control-Allow-Origin': 'http://localhost:3000', //     
      //       //  ,  
      //     }
      //   })
        axios({
          method: 'get',
          // url: 'https://chatbot.ext.lomger.tech/auth/users/',
          url: 'http://127.0.0.1:8000/brands',
          headers: {'Access-Control-Allow-Origin': 'http://localhost:3000',}
        })
        .then((response) => {
          setBrandList(response.data.map(item => item.Nazvanie_brand))
          console.log(BrandList)
          // console.log(selectedBrand)
          // console.log('mistake')
          
        })
        .catch((error) =>{ console.error(error); console.log('svfev')});
    };
    function handleRegionGet (selectedRegion) {
      setRegion(selectedRegion);
        axios({
          method: 'get',
          url: 'http://127.0.0.1:8000/regions',
          headers: {'Access-Control-Allow-Origin': 'http://localhost:3000',}
        })
        .then((response) => {
          setRegionList(response.data)
        })
        .catch((error) =>{ console.error(error); console.log('svfev')});
    };

    //       

    const handleModelsView = (brand) => {
      setBrand(brand)
      console.log(brand)
      axios.get('http://127.0.0.1:8000/models-by-brand/', { 
        method: 'GET',
        params: {
          Nazvanie_marki: brand
        },
        // body: JSON.stringify({
        //   brand: brand
        // }),
        headers: {
            "Content-type": "application/json; charset=UTF-8"
        }

      })
      .then(response => {
        //     
        setModelList(response.data.map(item => item.Nazvanie_modeli))
        // setBrand(e)
        console.log(response.data);
        console.log(ModelList)
        console.log(brand)
      })
      .catch(error => {
        //  
        console.error(error);
      });
      console.log(brand)
    }
    const handleModificationView = (model) => {
      // setBrand(brand)
      // console.log(brand)
      axios.get('http://127.0.0.1:8000/modification-by-model/', { 
        method: 'GET',
        params: {
          Nazvanie_modeli: model
        },
        // body: JSON.stringify({
        //   brand: brand
        // }),
        headers: {
            "Content-type": "application/json; charset=UTF-8"
        }

      })
      .then(response => {
        //     
        console.log(response.data)
        setModificationList(response.data)
        // setBrand(e)
        console.log(response.data);
        // console.log(ModelList)
        // console.log(brand)
      })
      .catch(error => {
        //  
        console.error(error);
      });
      console.log(brand)
    }


  const handleCountSum = (modification_id, probeg, region, rashod, cost) =>{
    console.log(region)
    const token = storedToken;
    axios.get('http://127.0.0.1:8000/cost_of_carship/', { 
        method: 'GET',
        params: {
          Region: region,
          Probeg: probeg,
          Modification: modification_id,
          Rashod_topliva: rashod,
          Cost_of_fuel: cost
        },
        headers: {
            "Content-type": "application/json; charset=UTF-8",
            // 'Authorization': `Bearer ${token}`  //    
            'Authorization': `Token ${token}`
        }

      })
      .then(response => {
        //     
        console.log(response.data)
        setSumma(response.data)
        // setModificationList(response.data)
        console.log(response.data);
      })
      .catch(error => {
        //  
        console.error(error);
      });
  }
  
  return (
    <div className="">
        <Header Store={AuthStore}/>
        <div className='container text-center' style={{padding: '50px'}}>
            <div className='row p-40' style={{padding: '10px'}}>
                <h5> </h5>
                <select className="form-select" onChange={(e) => {handleBrandChange(e.target.value);console.log(e.target.value); handleModelsView(e.target.value)}}>
                <option value="">  </option>
                    {BrandList &&
                    BrandList.map((brand) => (
                        <option key={brand} value={brand}>
                        {brand}
                        </option>
                    ))}
                </select>
            </div>
            <div className='row' style={{padding: '10px'}}>
                <h5> </h5>
                <select className="form-select" onChange={(e) => {handleModelChange(e.target.value); handleModificationView(e.target.value)}}>
                <option value="">  </option>
                
                {ModelList &&
                  ModelList.map((model) => (
                    <option key={model} value={model}>
                      {model}
                    </option>
                  ))}
                </select>
            </div>
            <div className='row' style={{padding: '10px'}}>
                <h5></h5>
                <select className="form-select" onChange={(e) => {handleModificationIdChange(e.target.value); console.log(e.target.value)}}>
                <option value="">  </option>
                
                {ModificatioinList &&
                  ModificatioinList.map((modification) => (
                    <option key={modification.id} value={modification.id}>
                      {modification.Power} .. {modification.Capacity_of_engine} ..
                    </option>
                  ))}
                </select>
            </div>
            <div className='row' style={{padding: '10px'}}>
                <h5> </h5>
                <select className="form-select" onChange={(e) => {handleRegionChange(e.target.value); console.log(e.target.value)}}>
                <option value=""> </option>
                
                {RegionList&&
                  RegionList.map((region) => (
                    <option key={region.id} value={region.id}>
                      {region.Nazvanie_regiona} 
                    </option>
                  ))}
                </select>
            </div>
            <div className='row' style={{padding: '10px'}}>
                <h5> </h5>
                <input type="number" class="form-control" id="exampleInput" placeholder="  " onChange={(e)=>handleProbegChange(e.target.value)}></input>
            </div>
            <div className='row' style={{padding: '10px'}}>
                <h5> </h5>
                <input type="number" class="form-control" id="exampleInput" placeholder="  " onChange={(e)=>handleCostChange(e.target.value)}></input>
            </div>
            <div className='row' style={{padding: '10px'}}>
                <h5> </h5>
                <input type="number" class="form-control" id="exampleInput" placeholder="  " onChange={(e)=>handleRashodChange(e.target.value)}></input>
            </div>
            <div className='container d-flex justify-content-center align-items-center' style={{padding: '10px'}}>
                <button className='btn btn-primary' onClick={() => {
                  handleRegionChange(Region);
                  handleCountSum(Modification_id, Probeg, Region, Rashod, FuelPrice)
                  handleCheckSumChange()
                  
                  console.log(Region)
                }}></button>
            </div>
            {checkSum &&(
              <>
                {/* <CountSum nalog={Probeg} toplivo={Rashod} summa={10}/> */}
                <div className="">
                  <div className='container'>
                      <p>: {Summa.nalog }</p>
                      <p> {Summa.toplivo}</p>
                      <p>  {Summa.sum_of_carship}</p>
                  </div>
                  

              </div>
              </>
              
            )}
        </div>
    </div>
  );
}

export default Calculator;